<template>
  <div>
    <main class="form-signin">
      <form @submit.prevent="signin">
        <h1 class="h3 mb-3 fw-normal text-center">Please Sign In</h1>
        <div class="form-floating">
          <label for="floatingInput">Email address</label>
          <input
            type="email"
            class="form-control"
            id="floatingInput"
            placeholder="name@example.com"
            v-model="user.username"
          />
        </div>
        <div class="form-floating mt-2">
          <label for="floatingPassword">Password</label>
          <input
            type="password"
            class="form-control"
            id="floatingPassword"
            placeholder="Password"
            v-model="user.password"
          />
        </div>

        <!-- <div class="checkbox mb-3">
          <label>
            <input type="checkbox" value="remember-me" /> Remember me
          </label>
        </div> -->
        <button class="w-100 btn btn-lg btn-primary" type="submit" :disabled="isActive">
          {{submitBtn}}
        </button>
        <span class="text-danger" v-if="!success">{{errorMsg}}</span>
        <p class="mt-3 mb-3 text-muted text-center">&copy; 2021-2022</p>
      </form>
    </main>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      user: {
        username: '',
        password: '',
      },
      success:true,
      errorMsg:'',
      submitBtn:'登入',
      isActive:false,
    };
  },
  methods: {
    signin() {
      const api = `${process.env.APIPATH}/admin/signin`;
      const vm = this;
      vm.submitBtn="登入中...";
      vm.isActive=true;
      this.$http.post(api,vm.user).then((response) => {
        vm.success=response.data.success;
        if(response.data.success){//登入成功導至首頁
          const token=response.data.token;
          const expired=response.data.expired;
          document.cookie=` hexToken=${token}; expires=${new Date(expired)};`;
          vm.$router.push('/admin/products');
        }else{
          vm.errorMsg=response.data.error.message;
          vm.submitBtn="登入";
          vm.isActive=false;
        }
      });
    },
  },
};
</script>
<style scoped>
html,
body {
  height: 100%;
}

body {
  display: flex;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}

.form-signin .checkbox {
  font-weight: 400;
}

.form-signin .form-floating:focus-within {
  z-index: 2;
}

.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>


