<template>
  <div>
    <nav aria-label="Page navigation example">
      <ul class="pagination justify-content-center">
        <li
          class="page-item"
          @click.prevent="previousPage()"
          :class="{ disabled: !!pagination.has_pre }"
        >
          <a class="page-link" href="#" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li
          class="page-item"
          v-for="page in pagination.total_pages"
          :key="page"
          :class="{ active: pagination.current_page == page }"
          @click.prevent="currentPage(page)"
        >
          <a class="page-link" href="#">{{ page }}</a>
        </li>
        <li
          class="page-item"
          @click.prevent="nextPage()"
          :class="{ disabled: !!pagination.has_next }"
        >
          <a class="page-link" href="#" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>
<script>
export default {
  name: "Pagination",
  data() {
    return {
      pagination: {},
    };
  },
  methods: {
    previousPage() {
      if (this.pagination.current_page - 1 > 0) {
        this.pagination.current_page = this.pagination.current_page - 1;
        this.$emit("passFunction", this.pagination.current_page);
      }
    },
    nextPage() {
      if (this.pagination.current_page + 1 <= this.pagination.total_pages) {
        this.pagination.current_page = this.pagination.current_page + 1;
        this.$emit("passFunction", this.pagination.current_page);
      }
    },
    currentPage(currentPage) {
      if (currentPage != this.pagination.current_page) {
        this.pagination.current_page = currentPage;
        this.$emit("passFunction", this.pagination.current_page);
      }
    },
  },
  created() {
    const vm = this;
    this.$bus.$on("page:change", (pagination) => {
      vm.pagination = pagination;
    });
  },
};
</script>


