<template>
  <div>
    <loading :active.sync="isLoading" />
    <div class="row mt-4">
      <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm">
          <div
            style="
              height: 150px;
              background-size: cover;
              background-position: center;
            "
            :style="{ backgroundImage: `url(${item.imageUrl})` }"
          ></div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{
              item.category
            }}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{ item.title }}</a>
            </h5>
            <p class="card-text">{{ item.content }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <del class="h6">原價 {{ item.origin_price | currency }} 元</del>
              <div class="h5">現在只要 {{ item.price | currency }} 元</div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button
              type="button"
              class="btn btn-outline-secondary btn-sm"
              @click="getProduct(item.id)"
            >
              <i
                class="fas fa-spinner fa-spin"
                v-if="status.DetailIsLoading"
              ></i>
              查看更多
            </button>
            <button
              type="button"
              class="btn btn-outline-danger btn-sm ml-auto"
              @click="addToCart(item.id)"
            >
              <i
                class="fas fa-spinner fa-spin"
                v-if="status.AddCartIsLoading"
              ></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>
    <pagination @passFunction="getProductsList"></pagination>
    <!-- 查看更多Modal -->
    <div
      class="modal fade"
      id="ProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>{{ product.title }}</span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt="" />
            <p class="mb-0">{{ product.content }}</p>
            <footer class="blockquote-footer text-align">
              {{ product.description }}
            </footer>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">
                {{ product.origin_price }}元
              </div>
              <del class="h6" v-if="product.price"
                >原價{{ product.origin_price }}元</del
              >
              <div class="h4" v-if="product.price">
                現在只要{{ product.price }}元
              </div>
            </div>
            <select name="" class="form-control mt-3" v-model="product.num">
              <option v-for="num in 10" :value="num" :key="num">
                選購{{ num }}{{ product.unit }}
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted tetxt-nowrap mr-3">
              小計<strong>{{
                (product.num * product.price) | currency
              }}</strong>
            </div>
            <button
              type="button"
              class="btn btn-primary"
              @click="addToCart(product.id, product.num)"
            >
              加入購物車
            </button>
          </div>
        </div>
      </div>
    </div>
    <div v-if="CartShowTag">
      <table class="table">
      <thead>
          <th></th>
          <th>品名</th>
          <th>數量</th>
          <th>單價</th>
      </thead>
      <tbody>
          <tr v-for="(item, key) in cart.carts" :key="key">
          <td class="align-middle">
              <button
              type="button"
              class="btn btn-outline-danger btn-sm"
              @click="delCartProduct(item.id)"
              >
              <i class="far fa-trash-alt"></i>
              </button>
          </td>
          <td class="align-middle">
              {{ item.product.title }}
              <div class="text-success" v-if="item.coupon">
                  已套用優惠券 : {{ item.coupon.title }}({{ item.coupon.code }}) - {{ item.coupon.percent }}%
              </div>
          </td>
          <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
          <td class="align-middle text-right">
              {{ item.final_total | currency }}
          </td>
          </tr>
      </tbody>
      <tfoot>
          <tr>
          <td colspan="3" class="text-right">原始價格總計</td>
          <td class="text-right">{{ cart.total | currency }}</td>
          </tr>
          <tr>
          <td colspan="3" class="text-right text-success">折扣價格總計</td>
          <td class="text-right text-success">
              {{ cart.final_total | currency }}
          </td>
          </tr>
      </tfoot>
      </table>
      <div class="input-group mb-3 input-group-sm">
        <input type="text" class="form-control" placeholder="請輸入優惠碼" v-model="couponCode"/>
        <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" @click="addCouponCdoe">
            套用優惠碼
            </button>
        </div>
      </div>
    </div>

    <div class="my-5 row justify-content-center">
        <validation-observer v-slot="{ invalid, handleSubmit }" class="col-md-12">
          <!-- 表單送出改為使用 form submit 的方法 -->
          <form @submit.prevent="handleSubmit(CreatOrder)">
            <validation-provider rules="required|email" class="form-group" tag="div" v-slot="{ errors, classes, passed }">
              <!-- 輸入框 -->
              <label for="email">Email</label>
              <input id="email" type="email" name="email" v-model="form.user.email"
                    class="form-control" :class="classes">
              <!-- 錯誤訊息 -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
              <span v-if="passed" class="valid-feedback">Email 正確</span>
            </validation-provider>
            <validation-provider rules="required" class="form-group" tag="div" v-slot="{ errors, classes, passed }">
              <!-- 輸入框 -->
              <label for="username">收件人姓名</label>
              <input id="username" type="text" name="name" v-model="form.user.name"
                    class="form-control" :class="classes">
              <!-- 錯誤訊息 -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </validation-provider>
            <validation-provider rules="required" class="form-group" tag="div" v-slot="{ errors, classes, passed }">
              <!-- 輸入框 -->
              <label for="usertel">收件人電話</label>
              <input id="usertel" type="text" name="tel" v-model="form.user.tel"
                    class="form-control" :class="classes">
              <!-- 錯誤訊息 -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </validation-provider>
            <validation-provider rules="required" class="form-group" tag="div" v-slot="{ errors, classes, passed }">
              <!-- 輸入框 -->
              <label for="useraddress">收件人地址</label>
              <input id="useraddress" type="text" name="address" v-model="form.user.address"
                    class="form-control" :class="classes">
              <!-- 錯誤訊息 -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </validation-provider>
            <div class="form-group">
              <label for="comment">留言</label>
              <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
            </div>
            <div class="text-right">
              <button class="btn btn-danger" :disabled="invalid">送出訂單</button>
            </div>
          </form>
        </validation-observer>
    </div>
  </div>
</template>
<script>
import $ from "jquery";
import Pagination from "../Pagination.vue";
export default {
  components: { Pagination },
  data() {
    return {
      products: {},
      product: {},
      isLoading: false,
      status: {
        DetailIsLoading: false,
        AddCartIsLoading: false,
      },
      cart: {},
      CartShowTag: false,
      couponCode:'',
      form:{
        user:{
          email:'',
          name:'',
          tel:'',
          address:'',
        },
        message:'',
      }
    };
  },
  methods: {
    getProductsList(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/products?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.isLoading = false;
        vm.products = response.data.products;
        console.log(vm.products)
        this.$bus.$emit("page:change", response.data.pagination);
      });
    },
    getProduct(id) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/product/${id}`;
      const vm = this;
      vm.status.DetailIsLoading = true;
      this.$http.get(api).then((response) => {
        $("#ProductModal").modal("show");
        vm.product = response.data.product;
        vm.status.DetailIsLoading = false;
      });
    },
    addToCart(id, qty = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/cart`;
      const vm = this;
      vm.status.AddCartIsLoading = true;
      const data = {
        product_id: id,
        qty: qty,
      };
      this.$http.post(api, { data: data }).then((response) => {
        $("#ProductModal").modal("hide");
        vm.status.AddCartIsLoading = false;
        this.getCartList();
      });
    },
    getCartList() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/cart`;
      const vm = this;
      this.$http.get(api).then((response) => {
        vm.cart = response.data.data;
        console.log(response.data.data)
        if (vm.cart.carts.length > 0) vm.CartShowTag = true;
        else vm.CartShowTag = false;
      });
    },
    delCartProduct(id) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(api).then((response) => {
        vm.$bus.$emit("message:push", response.data.message, "danger", 1);
        vm.getCartList();
        vm.isLoading = false;
      });
    },
    addCouponCdoe(){
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/coupon`;
        const vm = this;
        const data = {
            code: vm.couponCode,
        };
        this.$http.post(api, { data: data }).then((response) => {
            this.getCartList();
        });
    },
    CreatOrder(){
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/order`;
        const vm = this;
        const data = vm.form;
        vm.isLoading = true;
        // this.$validator.validate().then(valid => {
        //     if (valid) {
              this.$http.post(api, { data: data }).then((response) => {
                if (response.data.success) {
                  // vm.$bus.$emit("message:push", response.data.message, "success", 1);
                  // this.getCartList();
                  vm.$router.push(`/admin/customer-checkout/${response.data.orderId}`);
                  vm.isLoading = false;
                }
              });
        //     }
        // });
    }
  },
  created() {
    this.getProductsList();
    this.getCartList();
  },
};
</script>


