<template>
  <div>
    <loading :active.sync="isLoading" />
    <div class="text-right mt-4">
      <button
        class="btn btn-primary"
        type="button"
        data-bs-toggle="modal"
        data-bs-target="#coupnModal"
        @click="openModal(true)"
      >
        新增優惠卷
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th>優惠卷名稱</th>
          <th>優惠卷代碼</th>
          <th>優惠百分比</th>
          <th>到期日</th>
          <th width=100>是否啟用</th>
          <th width=150>功能</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td>{{ item.title }}</td>
          <td>{{ item.code }}</td>
          <td>{{ item.percent }}</td>
          <td>{{ item.due_date }}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
            <button
              class="btn btn-outline-primary btn-ms"
              @click="openModal(false, item)"
            >
              編輯
            </button>
            <button
              class="btn btn-outline-danger btn-ms"
              @click="openDelModal(item)"
            >
              刪除
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <pagination @passFunction="getCouponsList"></pagination>
    <!-- Modal -->
    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>新增優惠券</span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12">
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="title">優惠卷名稱</label>
                    <input
                      type="text"
                      class="form-control"
                      id="title"
                      placeholder="請輸入優惠卷名稱"
                      v-model="couponsTmp.title"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="code">優惠卷代碼</label>
                    <input
                      type="text"
                      class="form-control"
                      id="code"
                      placeholder="請輸入優惠卷代碼"
                      v-model="couponsTmp.code"
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="percent">優惠百分比</label>
                    <input
                      type="number"
                      class="form-control"
                      id="percent"
                      placeholder="請輸入優惠百分比"
                      v-model="couponsTmp.percent"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="due_date">到期日</label>
                    <input
                      type="date"
                      class="form-control"
                      id="due_date"
                      placeholder="請輸入到期日"
                      v-model="couponsTmp.due_date"
                    />
                  </div>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                      v-model="couponsTmp.is_enabled"
                      :true-value="1"
                      :false-value="0"
                    />
                    <label class="form-check-label" for="is_enabled">
                      是否啟用
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              取消
            </button>
            <button
              type="button"
              class="btn btn-primary"
              @click="updateCoupon"
            >
              確認
            </button>
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal fade"
      id="delCouponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除優惠卷</span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除
            <strong class="text-danger">{{ couponsTmp.title }}</strong>
            優惠卷(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              取消
            </button>
            <button type="button" class="btn btn-danger" @click="DelCoupon()">
              確認刪除
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import $ from "jquery";
import Pagination from "../Pagination";
export default {
  components: {
    Pagination,
  },
  data() {
    return {
      coupons: [],
      couponsTmp: {},
      pagination: {},
      isNew: false,
      isLoading: false,
    };
  },
  methods: {
    getCouponsList(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/admin/coupons?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.isLoading = false;
        vm.coupons = response.data.coupons;
        this.$bus.$emit("page:change", response.data.pagination);
      });
    },
    openModal(isNew, item) {
      $("#couponModal").modal("show");
      if (isNew) {
        this.isNew = true;
        this.couponsTmp = {};
      } else {
        this.couponsTmp = Object.assign({}, item);
        this.isNew = false;
      }
    },
    updateCoupon() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/admin/coupon`;
      let httpMethod = "post";
      const vm = this;
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/admin/coupon/${vm.couponsTmp.id}`;
        httpMethod = "put";
      }
      console.log(vm.couponsTmp)
      this.$http[httpMethod](api, { data: vm.couponsTmp }).then((response) => {
        if (response.data.success) {
          vm.$bus.$emit("message:push", response.data.message, "success", 2);
          $("#couponModal").modal("hide");
          vm.getCouponsList();
        } else {
          $("#couponModal").modal("hide");
          vm.getCouponsList();
          vm.$bus.$emit("message:push", response.data.message, "danger", 2);
        }
      });
    },
    openDelModal(item) {
      $("#delCouponModal").modal("show");
      this.couponsTmp = Object.assign({}, item);
    },
    DelCoupon() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/admin/coupon/${vm.couponsTmp.id}`;
      this.$http.delete(api).then((response) => {
        vm.$bus.$emit("message:push", response.data.message, "success", 2);
        $("#delCouponModal").modal("hide");
        vm.getCouponsList();
      });
    },
    uploadFile() {
      const vm = this;
      vm.status.fileUploading = true;
      const uploadFile = this.$refs.files.files[0];
      const formData = new FormData();
      formData.append("file-to-upload", uploadFile);
      vm;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/admin/upload`;
      this.$http
        .post(api, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          if (response.data.success) {
            vm.status.fileUploading = false;
            vm.$set(vm.couponsTmp, "imageUrl", response.data.imageUrl);
          } else {
            vm.$bus.$emit("message:push", response.data.message, "danger", 3);
          }
        });
    },
  },
  created() {
    this.getCouponsList();
  },
};
</script>


