<template>
    <div>
        <loading :active.sync="isLoading" />
        <div class="text-right mt-4">
        </div>
        <table class="table mt-4">
        <thead>
            <tr>
            <th width=150>單號</th>
            <th>購買時間</th>
            <th>Email</th>
            <th>購買款項</th>
            <th>應付金額</th>
            <th width=100>是否付款</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="item in orderList" :key="item.id">
            <td>{{ item.id }}</td>
            <td>{{ item.create_at | Date }}</td>
            <td>{{ item.user.email }}</td>
            <td>
                <ul v-for="pitem in item.products" :key="pitem.id" class="list-group">
                    <li class="list-group-item d-flex justify-content-between align-items-center">
                        {{pitem.product.title}} 
                        <span class="badge badge-primary badge-pill">{{pitem.qty}} {{pitem.product.unit}}</span>
                        <span class="text-success" v-if="pitem.coupon">
                            | 優惠券 : {{ pitem.coupon.title }}({{ pitem.coupon.code }}) - {{ pitem.coupon.percent }}%
                        </span>
                    </li>
                </ul>
            </td>
            <td>{{ item.total | Currency}}</td>
            <td>
                <span v-if="item.is_paid" class="text-success">已付款</span>
                <span v-else  class="text-danger">未付款</span>
            </td>
            </tr>
        </tbody>
        </table>
        <pagination @passFunction="getOrderList"></pagination>
    </div>
</template>
<script>
import Pagination from "../Pagination";
export default {
    components: {
        Pagination,
    },
    data(){
        return{
            orderList:{}
        }
    },
    methods: {
        getOrderList(page = 1) {
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/orders/?page=${page}`;
            const vm = this;
            vm.isLoading = true;
            this.$http.get(api).then((response) => {
                vm.isLoading = false;
                vm.orderList = response.data.orders;
                this.$bus.$emit("page:change", response.data.pagination);
            });
        }
    },
    created() {
    this.getOrderList();
  },
}
</script>


